.menu-card {
  width: 210px;
  height: 280px;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: font-size(card);
  margin: 0 10px;
  flex-shrink: 0;

  &__img {
    width: 144px;
    height: 144px;
    position: relative;
  }

  &__img img {
    width: 100%;
  }

  &__name {
    margin-top: 27px;
  }

  &__rating {
    position: absolute;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: center;
  }

  &__price {
    margin-top: 10px;
    font-weight: get-font-weight(big);
  }

  &__button {
    width: 105px;
    height: 30px;
    box-shadow: none;
    font-size: font-size(card);
    margin-top: 15px;
  }
}
